.tui-pagination {
  margin-bottom: 40px;
  margin-top: 40px;
  height: 40px;
  text-align: center;
  font-size: 0;
  @media (min-width: 768px) {
    margin-bottom: 60px;
    margin-top: 60px;
  }
}
.tui-pagination .tui-page-btn {
  display: inline-block;
  position: relative;
  width: 40px;
  height: 40px;
  font-size: 12px;
  margin-left: 5px;
  padding-top: 12px;
  padding-bottom: 12px;
  // padding: 12px;
  text-align: center;
  color: $text-color-primary;
  text-decoration: none;
  vertical-align: middle;
}
.tui-pagination .tui-page-btn:hover {
  background-color: $bg-color-secondary;
  border-radius: 5px;
}
.tui-pagination .tui-is-selected,
.tui-pagination strong {
  color: #fff;
  background: $card-accent-color;
  border-color: $card-accent-color;
  cursor: default;
  border-radius: 5px;
}
.tui-pagination .tui-is-selected:hover {
  background: $card-accent-color;
}

