:root {
  --Nhexa: 4;
  --gap: 2vw;
  --size: calc(calc(100vw / var(--Nhexa) )  - var(--gap) );
}

@media (min-width: 1100px) {
  :root {
    --Nhexa: 4;
  }
}

@media (max-width: 768px) {
  :root {
    --Nhexa: 2;
  }

  body {
    margin-right: calc(var(--size) * .3);
  }
}

* {
  position: relative;
}

.modalteam {
  padding-right: calc(var(--size) * .5);
  width: 90%;
  background-color: #fff;
  border: 0;
  border-radius: 15px;
  margin: auto;
  padding-top: 40px;
  padding-bottom: 30px;
  overflow-x: hidden;
}

.modalteam__section {
  width: calc(var(--size) * (var(--Nhexa)  - 1) );
  grid-template-columns: repeat(var(--Nhexa), 1fr);
  grid-gap: var(--gap);
  border-radius: 2px;
  display: grid;
  position: relative;
}

@media (min-width: 768px) {
  .modalteam__section {
    margin: calc(var(--size) * .3) auto 0;
  }
}

.modalteam__article {
  width: var(--size);
  height: calc(var(--size) / 1.11111);
  -webkit-clip-path: polygon(25% 0, 75% 0, 100% 50%, 75% 100%, 25% 100%, 0 50%);
  clip-path: polygon(25% 0, 75% 0, 100% 50%, 75% 100%, 25% 100%, 0 50%);
  margin-right: calc(var(--size) / 2);
  color: #fff;
  background: radial-gradient(circle at 50% 0, rgba(243, 108, 33, .2), rgba(243, 108, 33, 0) 70.71%), radial-gradient(circle at 6.7% 80%, rgba(127, 20, 22, .2), rgba(127, 20, 22, 0) 70.71%), beige radial-gradient(circle at 93.3% 80%, rgba(237, 31, 36, .2), rgba(237, 31, 36, 0) 70.71%);
  overflow: hidden;
}

.modalteam__article:nth-child(2n) {
  margin: calc(var(--size) * -.5) calc(var(--size) * -.25) 0 calc(var(--size) * -.75);
}

.modalteam__article:before {
  content: " ";
  float: left;
  width: 25%;
  height: 100%;
  -webkit-clip-path: polygon(0% 0%, 100% 0%, 0% 50%, 100% 100%, 0% 100%);
  clip-path: polygon(0% 0%, 100% 0%, 0% 50%, 100% 100%, 0% 100%);
  shape-outside: polygon(0% 0%, 100% 0%, 0% 50%, 100% 100%, 0% 100%);
}

.modalteam__img {
  width: var(--size);
  height: var(--size);
