.nav__list {
  display: flex;
  margin-left: auto;    
}

.nav__item:not(:first-child) {
  margin-left: 20px;

  @media screen and (min-width: 768px) {
    margin-left: 40px;
  }
}

.nav__link {
  position: relative;    
  font-size: 12px;
  line-height: 1.17;
  padding:10px 1px 3px;
  text-transform: uppercase;
  color: $text-color-active;   
}

.nav__link.active::after {   
  content: "";  
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;  
  width: 100%;
  height: 3px;   
  background-color: $active-page-color;
} 
